* {
    margin: 0;
    padding: 0;
    font-family: var(--font) !important;
    box-sizing: border-box;
    transition: all 0.15s ease;
}
/* scrollbar */
* {
    scrollbar-width: auto;
    scrollbar-color: #969696 #1c1c1c;
  }
  
*::-webkit-scrollbar {
    height: 5px;
    width: 5px;
  }

*::-webkit-scrollbar-track {
    background: transparent;
}

*::-webkit-scrollbar-thumb {
    background-color: #b9b9b9;
    border-radius: 10px;
    border: 0px solid #ffffff;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #a5a5a5;
}

/* default styles */
a {
    color: var(--black);
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
    cursor: pointer;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 16px;
}

.flex {
    display: flex !important;
}
.flex-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.flex-column {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
}
.jcsb {
    justify-content: space-between;
}
.jcc {
    justify-content: center;
}
.acc {
    align-content: center;
}
.acsb {
    align-content: space-between;
}
.jcfs {
    justify-content: flex-start;
}
.jcfe {
    justify-content: flex-end;
}
.wrap {
    flex-wrap: wrap;
}
.gap-6px {
    gap: 6px;
}
.gap-1 {
    gap: 1rem;
}
.gap-2 {
    gap: 2rem;
}
.gap-24px {
    gap: 24px;
}
.w40 {
    width: 40%;
}
.w50 {
    width: 50%;
}
.w100 {
    width: 100%;
}
.aifs {
    align-items: flex-start;
}
.aic {
    align-items: center;
}

.note, .note * {
    color: var(--colorSecond) !important;
    text-decoration-color: var(--colorSecond) !important;
    font-weight: 500;
}
.note2, .note2 * {
    color: var(--colorNote) !important;
    text-decoration-color: var(--colorNote) !important;
    font-weight: 500;
}

.wrap {
    flex-wrap: wrap;
}
.nowrap {
    flex-wrap: nowrap;
}